.accordion__item {
  padding: 0;
  list-style: none;
  margin-bottom: -1px;
}

.accordion__trigger {
  position: relative;
  color: #333;
  font-size: 13px;
  padding: 10px 50px 10px 20px;
  background-color: #fff;
  transition: 150ms all ease-in-out 0s;
  border: 1px solid #cdcdcd;
  border-radius: 3px;
  cursor: pointer;
  width: 100%;
  box-sizing: border-box;
  text-align: left;
}

.accordion__trigger:hover {
  background-color: #f9f9f9;
}

.accordion__trigger::after {
  transition: 150ms all ease-in-out 0s;
  position: absolute;
  right: 20px;
  top: 50%;
  margin-top: -5px;
  content: '';
  display: block;
  width: 6px;
  height: 6px;
  border-width: 0 1px 1px 0;
  border-color: #535353;
  border-style: solid;
  transform: rotate(45deg);
  transform-origin: 75% 75%;
}

.accordion__pannel {
  display: none;
  padding: 20px 10px;
}

.accordion__item_active .accordion__trigger {
  background-color: #f9f9f9;
}

.accordion__item_active .accordion__trigger::after {
  transform: rotate(225deg);
}

