.dropdown {
  font-size: 13px;
  position: relative;
  display: inline-block;
}

.dropdown__trigger {
  color: #333;
  border: 1px solid #cdcdcd;
  border-radius: 3px;
  background-color: #fff;
  font-size: 13px;
  line-height: 40px;
  padding: 4px 37px 4px 18px;
  cursor: pointer;
  user-select: none;
  transition: 125ms background-color ease-in-out;
}

.dropdown__trigger:hover {
  background-color: #f9f9f9;
}

.dropdown__caret {
  content: '';
  display: block;
  position: absolute;
  width: 6px;
  height: 6px;
  border-width: 0 1px 1px 0;
  border-style: solid;
  transform: rotate(45deg);
  transform-origin: 70% 70%;
  right: 18px;
  top: 50%;
  margin-top: -4px;
  transition: 150ms all ease-in-out 0s;
}

.dropdown__menu {
  min-width: 100%;
  width: 200px;
  max-width: 300px;
  position: absolute;
  left: 0;
  top: 100%;
  margin-top: -20px;
  z-index: 100;
  overflow: hidden;
  opacity: 0;
  visibility: hidden;
  transition: 125ms all ease-in-out;
  text-align: left;
  border-radius: 3px;
  background: #fff;
  box-shadow: 0 0 7px rgba(0,0,0,0.13);
  max-height: 170px;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}

.dropdown__menu ul {
  margin: 0;
  padding: 0;
}

.dropdown_open .dropdown__menu {
  opacity: 1;
  visibility: visible;
  z-index: 100;
  margin-top: 2px;
}

.dropdown_open .dropdown__caret {
  transform: rotate(225deg);
}

.dropdown_drop_down .dropdown__list {
  top: 100%;
  margin-top: -20px;
}

.dropdown_menu_right .dropdown__menu {
  left: auto;
  right: 0;
}

