.select {
  position: relative;
}

.select:after {
  content: '';
  display: block;
  position: absolute;
  z-index: 1;
}

.select__placeholder {
  display: none;
  visibility: hidden;
  opacity: 0;
  font-size: 0;
  height: 0;
  border: none;
  outline: none;
}

.select_multiple .select__control {
  height: auto !important;
}

.select_multiple:after {
  content: none !important;
}

