.sidebar-menu {
  background: #fff;
  background-color: #fff;
}

.sidebar-menu ul {
  margin: 0;
  padding: 0;
}

.sidebar-menu__item {
  font-size: 14px;
  padding: 0 0 18px 0;
  list-style: none;
}

.sidebar-menu__link,.sidebar-menu__link:visited {
  font-weight: bold;
  display: block;
  padding: 6px 20px 8px;
  text-decoration: none;
  color: #2d3e60;
  transition: 125ms all ease-in-out;
  position: relative;
}

.sidebar-menu__link:hover {
  color: #0074d9;
}

.sidebar-menu__link:before {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  height: 1px;
  width: 0;
  transition: width 0s ease, background 0.5s ease;
}

.sidebar-menu__link:after {
  content: '';
  display: block;
  position: absolute;
  right: 0;
  bottom: 0;
  height: 1px;
  width: 0;
  background: #0074d9;
  transition: width 0.5s ease;
}

.sidebar-menu__link:hover:before {
  width: 100%;
  background: #0074d9;
  transition: width 0.5s ease;
}

.sidebar-menu__link:hover:after {
  width: 100%;
  background: transparent;
  transition: all 0s ease;
}

.sidebar-menu__submenu .sidebar-menu__item {
  padding: 6px 30px 8px;
}

.sidebar-menu__submenu a,.sidebar-menu__submenu a:visited {
  text-decoration: none;
  color: #323232;
  transition: 125ms all ease-in-out;
  display: block;
  position: relative;
}

.sidebar-menu__submenu a:hover {
  text-decoration: underline;
  color: #0074d9;
}

