.tooltip {
  display: inline-block;
  background: rgba(97,97,97,0.9);
  border-radius: 2px;
  padding: 8px;
  font-size: 12px;
  color: #fff;
  max-width: 300px;
  position: fixed;
  top: -500px;
  left: -500px;
  z-index: 999;
  transform-origin: top center;
  opacity: 0;
  visibility: hidden;
  transition: 200ms opacity cubic-bezier(0, 0, 0.2, 1);
  line-height: 1.4em;
}

.tooltip_visible {
  opacity: 1;
  visibility: visible;
}

.tooltip:after {
  content: '';
  position: absolute;
}

.tooltip_bottom:after,.tooltip_top:after {
  left: 50%;
}

.tooltip_left:after,.tooltip_right:after {
  top: 50%;
}

.tooltip_bottom:after {
  width: 0;
  height: 0;
  border: 5px solid transparent;
  border-bottom-color: rgba(97,97,97,0.9);
  bottom: 100%;
  margin-left: -3px;
}

.tooltip_top:after {
  width: 0;
  height: 0;
  border: 5px solid transparent;
  border-top-color: rgba(97,97,97,0.9);
  top: 100%;
  margin-left: -3px;
}

.tooltip_left:after {
  width: 0;
  height: 0;
  border: 5px solid transparent;
  border-left-color: rgba(97,97,97,0.9);
  left: 100%;
  margin-top: -3px;
}

.tooltip_right:after {
  width: 0;
  height: 0;
  border: 5px solid transparent;
  border-right-color: rgba(97,97,97,0.9);
  right: 100%;
  margin-top: -3px;
}

